}

/********************************************************************
 * @MEDIA QUERIES ADICIONALES
 ********************************************************************/
@media (min-width: 2560px) {
  .c-header__container {
    max-width: 1800px;
    margin: 0 auto;
    padding: 20px 100px;
  }
}
.c-banner {
  position: relative;
  width: 100%;
  max-height: 100vh;
  overflow: hidden;
}
.c-banner .carousel,
.c-banner .carousel-inner,
.c-banner .carousel-item {
  height: 100%;
}
.c-banner .c-banner__item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.c-banner .c-banner__item a, .c-banner .c-banner__item picture {
  width: 100%;
  display: block;
}
.c-banner .c-banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}
.c-banner .c-banner__control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  z-index: 2;
  padding: 0;
}
.c-banner .c-banner__control i {
  font-size: 1.8rem;
  color: #222;
  transition: color 0.3s ease;
}
.c-banner .c-banner__control:hover i {
  color: #d05278;
}
.c-banner .c-banner__control--prev {
  left: 0.5rem;
}
.c-banner .c-banner__control--next {
  right: 0.5rem;
}
@media (max-width: 600px) {
  .c-banner .c-banner__control i {
    font-size: 1.3rem;
  }
  .c-banner .c-banner__control--prev {
    left: 0.3rem;
  }
  .c-banner .c-banner__control--next {
    right: 0.3rem;
  }
}

