/* =====================================================
   PURCHASE INFO (COMPRA, INPUTS Y BOTONES)
===================================================== */
.purchase-info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
  margin: 2rem 0;
  /* Media queries para purchase-info */
}
.purchase-info input {
  width: 4rem;
  border: 1px solid #ddd;
  border-radius: 2em;
  padding: 0.5em;
  appearance: textfield;
  -moz-appearance: textfield;
}
.purchase-info input::-webkit-outer-spin-button, .purchase-info input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.purchase-info .btn {
  flex: 1 1 auto;
  color: #fff;
  font-size: 1rem;
  padding: 0.75rem 1.5rem;
  background: #d05278;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-family: "Merienda", cursive;
  transition: background 0.3s, transform 0.3s;
  min-width: 150px;
  max-width: 250px;
}
.purchase-info .btn:hover {
  background: #b04860;
  transform: translateY(-2px);
}
.purchase-info .btn:active {
  background: #903850;
  transform: scale(0.98);
}
.purchase-info i {
  margin-left: 0.5rem;
  font-size: 1.2rem;
}
@media (max-width: 991px) {
